import React, { Component } from 'react';
import './HeaderButtons.css';

class HeaderButtons extends Component {
    render() {
      return (
        <div className="header-buttons">
          <i className="fas fa-video"></i>
          <img className="youtube-apps" alt="youtube-apps" src="data:image/svg+xml;utf8;base64," />
          <i className="fab fa-facebook-messenger"></i>
          <i className="fas fa-bell"></i>
          <img className="user-header-avatar" alt="user-header-avatar" src="https://yt3.ggpht.com/-9fPkv0S6GBY/AAAAAAAAAAI/AAAAAAAAAAA/_eiF_ifrxqU/s88-c-k-no-mo-rj-c0xffffff/photo.jpg"/>
        </div>
      );
    }
};

export default HeaderButtons;


